<template>
  <div class="repo-section">
    <div class="repo-title">
      <p v-for="(para, index) of intro" :key="index" class="para">{{ para }}</p>
    </div>
    <div class="repo-list">
      <div v-for="(repo, i) of repos" :key="i" class="repo-item">
        <h2>{{ repo.name }}</h2>
        <p>{{ repo.description }}</p>
        <p>{{ repo.html_url }}</p>
      </div>
    </div>
  </div>
</template>

<script>
const { Octokit } = require("@octokit/core");
const octokit = new Octokit();

export default {
  data() {
    return {
      intro: [
      'There are many public repositories on GitHub where you can directly access them on the their website or use officially provided API which contains a wealth of data available for developer to consume.',
      'For example, you can find my public repos on the following section. Each includes some basic information:'
      ],
      repos: []
    }
  },
  mounted() {
    this.getRepo()
  },
  methods: {
    async getRepo() {
      const user = 'damacusdos'
      let response = await octokit.request(`GET /users/${user}/repos`, {
        username: user
      })
      this.repos = response.data
      console.log(this.repos)
    }
  }
}
</script>

