<template>
  <div class="public-repo-section">
    <div class="public-repo-title">{{ title }}</div>
    <div class="public-repo-list">
      <div v-for="(repo, i) of repos" :key="i" class="public-repo-item">
        <h2>{{ repo.name }}</h2>
        <p>{{ repo.description }}</p>
        <p class="link">{{ repo.html_url }}</p>
      </div>
    </div>
  </div>
</template>

<script>
const { Octokit } = require("@octokit/core");
const octokit = new Octokit();

export default {
  props: ['showMore'],
  data() {
    return {
      title: 'And the following you can also find every public repositories on the GitHub:',
      repos: [],
    }
  },
  mounted() {
    this.getPublicRepo()
  },
  methods: {
    async getPublicRepo() {
      let response = await octokit.request('GET /repositories');
      this.repos = response.data
      console.log(this.repos)
    }
  }
}
</script>

